import React from 'react';

export const Today = (today) => {



    return (
        <div className="wather-app">
            <div>
                <img style={{ width: 200 }} src={require(`../img/animated/${today.today.list[0].weather[0].icon}.svg`).default} alt=" " />
            </div>
            <div>
                <h1>{today.today.city.name}</h1>
                <div style={{ fontSize: 24 }}>
                    <span>{today.today.list[0].weather[0].description}</span>
                    <span style={{ fontSize: 30, fontWeight: 700 }}> {Math.floor(today.today.list[0].main.temp)}C</span>
                </div>
                <h3>  {today.today.list[0].wind.gust}/</h3>
                <h3> {today.today.list[0].main.humidity}%</h3>
                <h4> : <strong>{today.today.list[0].pop * 100}%</strong></h4>
            </div>
            <div>
            </div>
        </div>
    )
}